import Constant from '../../util/constant.js'
export default {
	data() {
		return {
			defaultImg: Constant.defaultImg
		};
	},
	props: {
		item: {
			type: Object
		}
	},
	computed:{
		getImgUrl(){
			let item = this.item;
			if(item.state === 8){
				return item.coverImgs && item.coverImgs.length > 0?item.coverImgs[0]+ '?imageView2/1/w/750/h/498/interlace/1':this.defaultImg;
			}else{
				return item.surfaceImgs && item.surfaceImgs.length > 0? item.surfaceImgs[0].imgUrl + '?imageView2/1/w/750/h/498/interlace/1':this.defaultImg;
			}
		}
	},
	
